<template>
    <div class="france">
      <h1>Géographie</h1>
  
      La France est un État souverain transcontinental dont le territoire s'étend
      en Europe de l'Ouest et outremer (Indien, Atlantique, Pacifique et Amérique
      du Sud). Le pays a des frontières terrestres avec la Belgique, le
      Luxembourg, l'Allemagne, la Suisse, l'Italie, l'Espagne, Monaco et l'Andorre
      en Europe, auxquelles s'ajoutent les frontières terrestres avec le Brésil,
      le Suriname et les Pays-Bas aux Amériques. La France dispose d'importantes
      façades maritimes sur l'Atlantique, la Méditerranée, le Pacifique et l'océan
      Indien, lui permettant de bénéficier de la deuxième plus vaste zone
      économique exclusive du monde. La France est une république
      constitutionnelle unitaire ayant un régime semi-présidentiel. Elle a pour
      capitale Paris et pour langue officielle le français, langue de
      l'administration depuis 1539. Au 1er janvier 2022, la population de la
      France est d'environ 67,8 millions d'habitants.
    </div>
  </template>
  
  <script>
  export default {
    name: "france",
  
    metaInfo: {
      title: "Description générale de la France",
  
      meta: [
        {
          name: "description",
  
          content:
            "Devenez incollable sur la France, un pays un nul autre paraeil !!!",
        },
      ],
    },
  };
  </script>


